  --accent: 210 40% 96.1%;
  --accent-foreground: 222.2 47.4% 11.2%;
  --destructive: 0 84.2% 60.2%;
  --destructive-foreground: 210 40% 98%;
  --border: 214.3 31.8% 91.4%;
  --input: 214.3 31.8% 91.4%;
  --ring: 221.2 83.2% 53.3%;
  --radius: 0.25rem;
  --tv-background: 222 17% 11%;
  --tv-surface: 224 17% 15%;
  --tv-surface-elevated: 225 11% 20%;
  --tv-border: 225 9% 25%;
  --tv-text-primary: 220 13% 85%;
  --tv-text-secondary: 220 9% 55%;
  --tv-blue: 221 100% 59%;
  --tv-green: 164 89% 29%;
  --tv-red: 356 85% 55%;
}
.dark {
  --background: 222 17% 11%;
  --foreground: 220 13% 85%;
  --card: 224 17% 15%;
  --card-foreground: 220 13% 85%;
  --popover: 224 17% 15%;
  --popover-foreground: 220 13% 85%;
  --primary: 221 100% 59%;
  --primary-foreground: 220 13% 95%;
  --secondary: 225 11% 20%;
  --secondary-foreground: 220 13% 85%;
  --muted: 225 11% 20%;
  --muted-foreground: 220 9% 55%;
  --accent: 225 11% 20%;
  --accent-foreground: 220 13% 85%;
  --destructive: 356 85% 55%;
  --destructive-foreground: 220 13% 95%;
  --border: 225 9% 25%;
  --input: 225 11% 20%;
  --ring: 221 100% 59%;
  --radius: 0.25rem;
}
* {
  border-color: hsl(var(--border));
}
body {
  background-color: hsl(var(--background));
  color: hsl(var(--foreground));
  font-family: -apple-system, BlinkMacSystemFont, 'Trebuchet MS', Roboto, Ubuntu, sans-serif;
}
.tv-panel {
  background: hsl(var(--card));
  border: 1px solid hsl(var(--border));
}
.tv-toolbar {
  background: hsl(var(--card));
  border-bottom: 1px solid hsl(var(--border));
  height: 40px;
  display: flex;
  align-items: center;
  padding: 0 12px;
  gap: 8px;
}
.tv-button {
  background: transparent;
  border: none;
  color: hsl(var(--muted-foreground));
  padding: 6px 12px;
  border-radius: 4px;
  font-size: 13px;
  cursor: pointer;
  transition: all 0.2s;
  font-weight: 500;
}
.tv-button:hover {
  background: hsl(var(--secondary));
  color: hsl(var(--foreground));
}
.tv-button.active {
  background: hsl(var(--primary) / 0.15);
  color: hsl(var(--primary));
}
.tv-buy-button {
  background: hsl(var(--tv-green));
  color: white;
  font-weight: 600;
}
.tv-buy-button:hover {
  background: hsl(var(--tv-green) / 0.9);
  color: white;
}
.tv-sell-button {
  background: hsl(var(--tv-red));
  color: white;
  font-weight: 600;
}
.tv-sell-button:hover {
  background: hsl(var(--tv-red) / 0.9);
  color: white;
}
.tv-input {
  background: hsl(var(--input));
  border: 1px solid hsl(var(--border));
  color: hsl(var(--foreground));
  padding: 6px 10px;
  border-radius: 4px;
  font-size: 13px;
  outline: none;
  transition: border 0.2s;
}
.tv-input:focus {
  border-color: hsl(var(--primary));
}
.tv-separator {
  width: 1px;
  height: 20px;
  background: hsl(var(--border));
  margin: 0 4px;
}
::-webkit-scrollbar {
  width: 6px;
  height: 6px;
}
::-webkit-scrollbar-track {
  background: hsl(var(--background));
}
::-webkit-scrollbar-thumb {
  background: hsl(var(--border));
  border-radius: 3px;
}
::-webkit-scrollbar-thumb:hover {
  background: hsl(var(--muted-foreground));
}
button {
  font-family: inherit;
}
.neumorphic-button::after {
  content: '';
  position: absolute;
  inset: 0;
  opacity: 0;
  transition-property: opacity;
  transition-duration: 300ms;
  background: linear-gradient(to bottom right, rgba(155, 135, 245, 0.2), transparent);
  border-radius: 9999px;
}
.neumorphic-button:hover::after {
  opacity: 1;
}
